export const videoModal = () => {
    $('.js-video-modal').magnificPopup({
        type: 'iframe',
        mainClass: 'mfp-fade',
        tClose: '',
        tLoading: '',
        fixedContentPos: false,
        removalDelay: 160,
        preloader: false,
        callbacks: {
            open: () => {
                $('body').addClass('overflow');
            },
            close: () => {
                $('body').removeClass('overflow');
            }
        }
    });
};
export const simpleModal = () => {
    $('.js-simple-modal').magnificPopup({
        type: 'inline',
        mainClass: 'mfp-fade',
        tClose: '',
        tLoading: '',
        removalDelay: 500,
        closeBtnInside: true,
        fixedContentPos: true,
        preloader: false,
        closeMarkup: '<button title="%title%" type="button" class="mfp-close"><i class="icon icon-close"></i></button>',
        callbacks: {
            open: () => {
                $('body').addClass('overflow');
            },
            close: () => {
                $('body').removeClass('overflow');
            }
        }
    });
};
export const galleryModal = (galleryBlock) => {
    $(galleryBlock).magnificPopup({
        delegate: 'a',
        type: 'image',
        mainClass: 'mfp-fade',
        tClose: '',
        tLoading: '',
        gallery: {
            enabled: true,
            tPrev: '',
            tNext: '',
            tCounter: '<span class="mfp-counter"><span class="active">%curr%</span> / <span>%total%</span></span>'
        },
        image: {
            tError: ' ',
        }
    });
};