import 'lazysizes/plugins/bgset/ls.bgset.js';
import 'lazysizes/plugins/parent-fit/ls.parent-fit.js';
import gsap from "gsap";
import ScrollTrigger from "gsap/ScrollTrigger";
// slider
import Swiper, {Pagination, Parallax, Mousewheel} from "swiper";
// modal
import {simpleModal, videoModal} from "./_func/_jq/modalMagnific";
/* ------------------- */

document.addEventListener('DOMContentLoaded', () => {
    gsap.registerPlugin(ScrollTrigger);
    /* main slider */
    var mainSlider = new Swiper("#main-slider", {
        modules: [Parallax, Mousewheel],
        speed: 1800,
        parallax: true,
        direction: "vertical",
        watchSlidesProgress: true,
        mousewheelControl: true,
        mousewheel: true
    });
    mainSlider.on('slideChange', function (){
        console.log('test');
    })

    /* main page sliders */
    var qualitySlider = new Swiper("#quality-slider", {
        modules: [ Pagination ],
        speed: 800,
        spaceBetween: 0,
        pagination: {
            el: ".swiper-pagination",
            clickable: true,
        }
    });
    document.querySelectorAll('.main-popular .popular-slider').forEach(popularSlider => {
        var popularSlider = new Swiper(popularSlider, {
            speed: 800,
            loop: false,
            slidesPerView: "auto",
            spaceBetween: 0
        });
    });
    // change popular sliders
    $('.main-popular .popular-list .popular-list__link').click(function (){
        var curType = $(this).attr('data-type');
        $('.main-popular .popular-list .popular-list__link').removeClass('active');
        $('.main-popular .main-popular__content .popular-slider').hide();
        $(this).addClass('active');
        $('.main-popular .main-popular__content .popular-slider[data-type="'+ curType +'"]').fadeIn(300);
    });
    var fathersSlider = new Swiper("#fathers-slider", {
        modules: [ Pagination ],
        speed: 800,
        spaceBetween: 0,
        pagination: {
            el: ".swiper-pagination",
            clickable: true,
        }
    });
    var lifehacksSlider = new Swiper('#lifehacks-slider', {
        modules: [ Pagination ],
        speed: 800,
        loop: false,
        slidesPerView: "auto",
        spaceBetween: 0,
        pagination: {
            el: ".swiper-pagination",
            clickable: true,
        }
    });
    /* phone mask */
    $("input[name='phone']").mask("+7 (999) 999-9999");
    /* file function */
    /* TODO */

    /* quiz links */
    $('#quiz_modal input[name="age"]').change(function (){
        $('#quiz_modal .modal-content__step[data-step="5"] .next-link').attr('data-step', $('#quiz_modal input[name="age"]:checked').attr('data-step'));
    });
    $('.js-step-btn').click(function (){
        var curStep = $(this).attr('data-step');
        $('#quiz_modal .modal-content__step').hide();
        $('#quiz_modal .modal-content__step[data-step="'+ curStep +'"]').fadeIn(500);
    })
    /* modal */
    simpleModal();
    videoModal();

    // tmp
    /*
    setTimeout(function (){
        $('#history_modal .modal-form-wrapper').hide();
        $('#history_modal .modal-message.success-message').fadeIn();
    }, 4000)
     */
})